/* ------------ GLOBAL DESIGN THEME ------------ */
:root{
  --primary: #DCC7AA;
  --secondary: #A68A64;
  --accent: #6B4F3B;
  --bg: #F8F4EF;
  --text: #3E2C20;
  --muted: #8A7360;
  --card-radius: 14px;
}

*{
  box-sizing:border-box;
  margin:0;
  padding:0;
}

html,body{
  height:100%;
  font-family: "Neue Montreal", "Poppins", "Playfair Display", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
}

body{
  color:var(--text);
  background:linear-gradient(180deg, var(--bg) 0%, #faf7f3 100%);
  -webkit-font-smoothing:antialiased;
  line-height:1.6;
}

.container{
  width: 100%;
  padding: 0;
  margin: 0;
}

/* ------------ HEADER ------------ */
header{
  display:flex;
  align-items:center;
  justify-content:space-between;
  padding:16px 20px;
  width:100%;
  background:linear-gradient(180deg, #fff, rgba(248,244,239,0.9));
  border-bottom:1px solid rgba(166,138,100,0.1);
  box-shadow:0 2px 10px rgba(166,138,100,0.05);
  position:sticky;
  top:0;
  z-index:1000;
}

.brand{
  display:flex;
  align-items:center;
  gap:14px;
  text-decoration:none;
  color:var(--text);
}

.logo{
  width:50px; height:50px;
  border-radius:10px;
  background:linear-gradient(135deg,var(--primary),var(--secondary));
  display:grid; place-items:center;
  font-family:"Playfair Display", serif;
  font-size:20px; font-weight:700;
  color:white;
  box-shadow:0 4px 12px rgba(107,79,59,0.1);
}

nav{display:flex; gap:8px; align-items:center}

.nav-btn{
  background:transparent;
  border:1px solid transparent;
  padding:8px 16px;
  border-radius:20px;
  font-weight:500;
  color:var(--text);
  cursor:pointer;
  font-size:14px;
  transition:all 0.2s ease;
  font-family: "Neue Montreal", "Poppins", sans-serif;
}

.nav-btn:hover{
  background:rgba(166,138,100,0.1);
  border-color:rgba(166,138,100,0.3);
}

.nav-btn.primary{
  background:var(--accent);
  color:white;
  border-color:var(--accent);
  box-shadow:0 4px 12px rgba(106,82,58,0.12);
}

/* ------------ HERO SECTION ------------ */
.hero{
  display:grid;
  grid-template-columns:1fr 420px;
  gap:30px;
  align-items:center;
  padding:30px 20px;
  border-radius:18px;
  border:1px solid rgba(166,138,100,0.08);
  background: linear-gradient(180deg, rgba(220,199,170,0.2), rgba(255,255,255,0.0));
  margin: 20px 0;
}

.hero-left h2{
  font-family:"Playfair Display", serif;
  font-size:42px;
  color:var(--accent);
  margin-bottom:12px;
  line-height: 1.2;
}

.cta-row{display:flex; gap:16px}
.cta{
  padding:14px 24px;
  border-radius:12px;
  background:var(--accent);
  border:1px solid rgba(107,79,59,0.06);
  font-weight:600;
  cursor:pointer;
  color:#fff;
  font-family: "Neue Montreal", "Poppins", sans-serif;
  font-size: 16px;
  transition: all 0.2s ease;
}
.cta:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(106,82,58,0.2);
}
.secondary-cta{
  background:transparent;
  border:2px solid var(--accent);
  color:var(--accent);
  background: transparent;
}
.secondary-cta:hover {
  background: rgba(166,138,100,0.1);
}

.badges{display:flex; gap:20px; margin-top:18px}
.badge{
  background:rgba(166,138,100,0.08);
  padding:8px 12px;
  border-radius:10px;
  color:var(--secondary);
  font-size:15px;
  display: flex;
  align-items: center;
  gap: 8px;
}

/* ------------ CATEGORIES ------------ */
.section-title{
  display:flex;
  justify-content:space-between;
  align-items:center;
  margin:30px 0 20px;
}

.section-title h3{
  font-family:"Playfair Display", serif;
  color:var(--accent);
  font-size:28px;
  margin: 0;
}

.chips{display:flex; gap:12px; flex-wrap:wrap; margin: 0;}
.chip{
  background:linear-gradient(180deg, #fff, rgba(220,199,170,0.14));
  padding:8px 16px;
  border-radius:999px;
  font-size:14px;
  color:var(--secondary);
  border:1px solid rgba(166,138,100,0.1);
  cursor: pointer;
  transition: all 0.2s ease;
  font-family: "Neue Montreal", "Poppins", sans-serif;
}
.chip:hover {
  background: rgba(166,138,100,0.2);
  border-color: rgba(166,138,100,0.3);
}

/* ------------ PRODUCT GRID ------------ */
.grid{
  display:grid;
  grid-template-columns:repeat(auto-fit,minmax(240px,1fr));
  gap:24px;
  margin: 0 0 40px;
}

.card{
  background:linear-gradient(180deg, #fff, rgba(248,244,239,0.9));
  padding:16px;
  border-radius:var(--card-radius);
  border:1px solid rgba(166,138,100,0.1);
  transition:all 0.3s ease;
  box-shadow: 0 4px 12px rgba(166,138,100,0.08);
}
.card:hover{
  transform:translateY(-6px);
  box-shadow:0 16px 40px rgba(166,138,100,0.15);
}

.product-img{
  width:100%;
  height:200px;
  border-radius:12px;
  background-size:cover;
  margin-bottom:14px;
}

.product-name{
  font-weight:600; 
  color:var(--accent); 
  margin-bottom:6px;
  font-family: "Neue Montreal", "Poppins", sans-serif;
  font-size: 18px;
}

.product-meta{
  font-size:14px; 
  color:var(--muted); 
  margin-bottom:12px;
  font-family: "Neue Montreal", "Poppins", sans-serif;
}

.price-row{
  display:flex;
  justify-content:space-between;
  align-items:center;
}

.add-btn{
  padding:8px 14px;
  background:var(--accent);
  color:white;
  border:none;
  border-radius:10px;
  cursor:pointer;
  font-family: "Neue Montreal", "Poppins", sans-serif;
  font-weight: 500;
  transition: all 0.2s ease;
}
.add-btn:hover {
  opacity: 0.9;
  transform: translateY(-1px);
}

/* ------------ FOOTER ------------ */
footer{
  margin:50px 0 30px;
  border-top:1px dashed rgba(166,138,100,0.1);
  padding-top:20px;
  color:var(--muted);
  display:flex;
  justify-content:space-between;
  font-family: "Neue Montreal", "Poppins", sans-serif;
}

/* LOGIN PAGE STYLING */
.login-wrapper{
  max-width: 420px;
  margin: 60px auto;
  background: linear-gradient(180deg,#fff,rgba(248,244,239,0.9));
  border-radius: 16px;
  padding: 32px;
  border:1px solid rgba(166,138,100,0.1);
  box-shadow:0 8px 24px rgba(166,138,100,0.1);
}

.login-wrapper h2{
  font-family:"Playfair Display",serif;
  font-size:32px;
  color:var(--accent);
  margin-bottom:16px;
  text-align: center;
}

.login-wrapper p{
  color:var(--muted);
  margin-bottom:20px;
  font-size:15px;
  text-align: center;
  font-family: "Neue Montreal", "Poppins", sans-serif;
}

.login-row{
  margin-bottom:18px;
  display:flex;
  flex-direction:column;
}

.login-label{
  font-family: "Neue Montreal", "Poppins", sans-serif;
  color: var(--text);
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 500;
}

.login-input{
  padding:14px 16px;
  border-radius:12px;
  border:1px solid rgba(166,138,100,0.2);
  font-size:15px;
  font-family: "Neue Montreal", "Poppins", sans-serif;
  transition: all 0.2s ease;
}
.login-input:focus {
  outline: none;
  border-color: rgba(166,138,100,0.5);
  box-shadow: 0 0 0 3px rgba(166,138,100,0.1);
}

.login-btn{
  margin-top:16px;
  padding:14px;
  background:var(--accent);
  border:none;
  border-radius:12px;
  color:#fff;
  font-weight:600;
  cursor:pointer;
  font-family: "Neue Montreal", "Poppins", sans-serif;
  font-size: 16px;
  transition: all 0.2s ease;
}
.login-btn:hover {
  opacity: 0.9;
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(106,82,58,0.2);
}

.login-link{
  margin-top:16px;
  color:var(--accent);
  font-size:14px;
  cursor:pointer;
  text-align: center;
  font-family: "Neue Montreal", "Poppins", sans-serif;
  font-weight: 500;
}
.login-link:hover {
  text-decoration: underline;
}